import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Article } from './article';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';

// Add the RxJS Observable operators.
import './../rxjs-operators';

@Injectable()
export class ArticleService {
  private articlesUrl = '/api/articles/';
  private articleUrl = '/api/article/';

  constructor(private http: Http, private router: Router) { }

  getArticles(): Observable<Article[]> {
    const articles$ = this.http.get(this.articlesUrl)
      .map(this.extractArticles)
      .catch(this.handleError);

    return articles$;
  }

  getArticle(name: string): Observable<Article[]> {
    const articleUrl = this.articleUrl + name;
    const articles$ = this.http.get(articleUrl)
      .map(this.extractArticles);

    return articles$;
  }

  private extractArticles(res: Response) {
    const object = res.json();
    return object.Results || {};
  }

  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



