import { InternalServerErrorComponent } from './../errorHandlers/internalServerError.component';
import { NotFoundComponent } from './../errorHandlers/notFound.component';
import { ArticleComponent } from './../article/article.component';
import { AppComponent } from './app.component';
import { NgModule, Component } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ArticleListComponent } from './../article/articleList.component';
import { ArticleService } from './../article/article.service';
import appRoutes  from './app.routes';

@NgModule({
  imports: [BrowserModule, HttpModule, appRoutes],
  declarations: [AppComponent, ArticleListComponent, ArticleComponent,
   NotFoundComponent, InternalServerErrorComponent],
  bootstrap: [AppComponent],
  providers: [ArticleService]
})
export class AppModule { }



