import { InternalServerErrorComponent } from './../errorHandlers/internalServerError.component';
import { NotFoundComponent } from './../errorHandlers/notFound.component';
import { ArticleComponent } from './../article/article.component';
import { Component } from '@angular/core';
import { ArticleListComponent } from './../article/articleList.component';
import { RouterModule } from '@angular/router';

const routes = [
  { path: '', component: ArticleListComponent },
  { path: '404', component: NotFoundComponent },
  { path: '500', component: InternalServerErrorComponent },
  { path: ':name', component: ArticleComponent }
]

 export default RouterModule.forRoot(routes)


